



















import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { CountDown } from 'vant';
import { TimeValue, TimeOptions } from '@/globalInterface';

@Component
export default class ReportTime extends Vue {
  @Prop({type: Object, default: () => ({})}) public readonly time!: TimeOptions;
  private locTime?: number = 0;
  private locMaxTime?: number = 0;
  private progress?: number = 0;
  private timeString?: string = '00:00:00';
  private showVideo() {
    console.log('show vid')
  }
  
  private convertToSeconds(v: TimeValue) {
    const daysInSec = v.days * 24 * 60 * 60;
    const hrsInSec = v.hours * 60 * 60;
    const minInSec = v.minutes * 60;

    let stringTime = '';
    if (daysInSec !== 0) {
      stringTime += `${this.pad(v.days)}:`;
    }
    if (hrsInSec !== 0) {
      stringTime += `${this.pad(v.hours)}:`
    }
    return {
      actualMilSec: (daysInSec + hrsInSec + minInSec + v.seconds) * 1000,
      stringTime: `${stringTime}${this.pad(v.minutes)}:${this.pad(v.seconds)}`
    };
  }
  private pad(n: any) {
    const z = '0';
    n = n + '';
    return n.length >= 2 ? n : new Array(2 - n.length + 1).join(z) + n;
  }
  private locTimeChange(v: TimeValue) {
    const value = this.convertToSeconds(v);
    this.progress = (value.actualMilSec / (this.locMaxTime || 0)) * 100;
    this.timeString = value.stringTime;
  }
  private timeFinish() {
    // NOTE Simulation of API call
    this.$emit('done-timer');
  }
  private mounted() {
    this.timeChange(this.time);
  }
  @Watch('time')
  private timeChange(value:  TimeOptions) {
    this.locTime = value.current * 1000;
    this.locMaxTime = value.max * 1000;
    this.progress = 100;
    setTimeout(() => {
      (this.$refs.countDown as CountDown).start()
    }, 100)
  }
}
